import React, { useState, useEffect } from 'react';
import { Calendar, Clock, Users, Briefcase, User, CheckCircle, AlertCircle, ArrowUpCircle, ArrowDownCircle } from 'lucide-react';

const ActionTracker = () => {
  // Sample data
  const initialItems = [
    { 
      id: 1, 
      title: 'Q2 Budget Review', 
      description: 'Review and approve Q2 budget allocations', 
      category: 'executive', 
      priority: 'high',
      status: 'in-progress', 
      dueDate: '2025-05-01', 
      assignee: 'Self'
    },
    { 
      id: 2, 
      title: 'Partnership Meeting', 
      description: 'Discuss next steps with CloudTech', 
      category: 'partner', 
      priority: 'high',
      status: 'not-started', 
      dueDate: '2025-04-28', 
      assignee: 'Legal Team'
    },
    { 
      id: 3, 
      title: 'Team Quarterly Goals', 
      description: 'Set Q2 OKRs with team', 
      category: 'team', 
      priority: 'medium',
      status: 'completed', 
      dueDate: '2025-04-15', 
      assignee: 'Self'
    },
    { 
      id: 4, 
      title: 'Professional Development Plan', 
      description: 'Update personal development roadmap', 
      category: 'personal', 
      priority: 'low',
      status: 'not-started', 
      dueDate: '2025-05-15', 
      assignee: 'Self'
    },
  ];

  // State for items and filters
  const [items, setItems] = useState(initialItems);
  const [filteredItems, setFilteredItems] = useState(initialItems);
  const [filters, setFilters] = useState({
    category: 'all',
    status: 'all'
  });

  // Apply filters when filters or items change
  useEffect(() => {
    let result = [...items];
    
    if (filters.category !== 'all') {
      result = result.filter(item => item.category === filters.category);
    }
    
    if (filters.status !== 'all') {
      result = result.filter(item => item.status === filters.status);
    }
    
    setFilteredItems(result);
  }, [filters, items]);

  // Handle filter changes
  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Format date for display
  const formatDate = (dateString) => {
    const options = { year: 'numeric', month: 'short', day: 'numeric' };
    return new Date(dateString).toLocaleDateString(undefined, options);
  };

  return (
    <div className="min-h-screen bg-slate-100 p-4">
      <div className="max-w-6xl mx-auto">
        <header className="mb-8 bg-white p-6 rounded-xl shadow-sm border border-slate-200">
          <h1 className="text-3xl font-bold text-slate-800">Action Tracker</h1>
          <p className="text-slate-600 pl-1">Track, prioritize, and manage your requests</p>
        </header>

        {/* Filters */}
        <div className="mb-6 bg-white p-4 rounded-xl shadow-sm border border-slate-200">
          <div className="flex flex-wrap gap-3">
            <div className="flex items-center">
              <span className="text-sm text-slate-700 mr-2">Filter by:</span>
            </div>
            
            <select 
              name="category" 
              value={filters.category} 
              onChange={handleFilterChange}
              className="bg-white border border-slate-200 rounded-lg px-3 py-1.5 text-sm shadow-sm"
            >
              <option value="all">All Categories</option>
              <option value="partner">Partner</option>
              <option value="executive">Executive</option>
              <option value="team">Team</option>
              <option value="personal">Personal</option>
            </select>
            
            <select 
              name="status" 
              value={filters.status} 
              onChange={handleFilterChange}
              className="bg-white border border-slate-200 rounded-lg px-3 py-1.5 text-sm shadow-sm"
            >
              <option value="all">All Statuses</option>
              <option value="not-started">Not Started</option>
              <option value="in-progress">In Progress</option>
              <option value="completed">Completed</option>
            </select>
          </div>
        </div>

        {/* Items list */}
        <div className="space-y-4">
          {filteredItems.length > 0 ? (
            filteredItems.map((item) => (
              <div key={item.id} className="bg-white p-4 rounded-xl shadow-sm border border-slate-200">
                <div className="flex flex-wrap justify-between items-start">
                  <div>
                    <h2 className="text-lg font-semibold text-slate-800">{item.title}</h2>
                    <p className="text-slate-600 text-sm">{item.description}</p>
                  </div>
                  <div className="flex flex-wrap gap-2 mt-1">
                    <span className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium capitalize 
                      ${item.category === 'team' ? 'team-badge' : 
                        item.category === 'personal' ? 'personal-badge' :
                        item.category === 'partner' ? 'bg-blue-100 text-blue-800' :
                        'bg-purple-100 text-purple-800'}`}>
                      {item.category}
                    </span>
                    <span className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium capitalize
                      ${item.priority === 'high' ? 'bg-red-100 text-red-800' :
                        item.priority === 'medium' ? 'bg-yellow-100 text-yellow-800' :
                        'bg-green-100 text-green-800'}`}>
                      {item.priority}
                    </span>
                    <span className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium capitalize
                      ${item.status === 'completed' ? 'bg-emerald-100 text-emerald-800' :
                        item.status === 'in-progress' ? 'bg-blue-100 text-blue-800' :
                        'bg-gray-100 text-gray-800'}`}>
                      {item.status.replace('-', ' ')}
                    </span>
                  </div>
                </div>
                <div className="mt-3 pt-3 border-t border-slate-100 flex flex-wrap justify-between items-center text-sm">
                  <div className="flex items-center text-slate-600">
                    <Calendar className="h-4 w-4 mr-1.5" />
                    <span>{formatDate(item.dueDate)}</span>
                  </div>
                  <div className="text-slate-600">
                    <span>Assigned to: {item.assignee}</span>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div className="bg-white p-8 rounded-xl shadow-sm border border-slate-200 text-center">
              <p className="text-slate-500">No items found matching your filters.</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ActionTracker;